type GetAutosuggestSourceRowStatusProps = {
  isSource: boolean
  sourceSelectionPackages: Array<string> | null
  isSourceSelected?: boolean
  isPackageSelected?: boolean
  id?: string
}

type GetAutosuggestSourceRowStatusState = {
  isDisabled: boolean
  isThisCheckboxChecked: boolean
  tooltipText: string | null
}

function getAutosuggestSourceRowStatus({
  isSource,
  sourceSelectionPackages,
  isSourceSelected,
  isPackageSelected,
}: GetAutosuggestSourceRowStatusProps): GetAutosuggestSourceRowStatusState {
  const isSelectedAsItself = !!(isSource ? isSourceSelected : isPackageSelected)
  const hasPackageIntersection = !!sourceSelectionPackages

  const isSelected = hasPackageIntersection || isSelectedAsItself
  const isDisabled = isSource && hasPackageIntersection

  const tooltipText = sourceSelectionPackages
    ? `Selected in ${
        sourceSelectionPackages?.length === 1 ? "package" : "packages"
      }: ${sourceSelectionPackages?.join(", ")}`
    : null

  return {
    isDisabled,
    isThisCheckboxChecked: isSelected,
    tooltipText,
  }
}

export { getAutosuggestSourceRowStatus }
